.side {
    grid-area: side;
    background: rgba(197, 124, 124, 0.5);
    -webkit-backdrop-filter: blur(0.75px);
    backdrop-filter: blur(0.75px);
    border: 1px solid rgba(0, 0, 0, 0.4);
    position: relative;


    .picture {
        margin-top: 15px;
        width: 200px;
        height: 200px;
        opacity: 0.9;
        border-radius: 50%;
    }

    .name {
        margin-top: 5px;
        color: rgb(182, 255, 255);
        font-size: 32px;
    }

    .social-buttons {
        background: rgba(188, 61, 61, 0.67);
        height: 60px;
        margin-top: 14px;
    }
}


@media (max-width: 780px) {
    .side {
        .picture {
            width: 100px;
            height: 100px;
            position: absolute;
            left: 10px;
            bottom: 8px;
        }

        .name {
            position: absolute;
            left: 50%;
            transform: translate(-50%);
        }

        .social-buttons {
            position: absolute;
            left: 50%;
            top: 33px;
            translate: (-50%);
        }
    }
}


@media (max-width: 530px) {
    .side {
        .name {
            position: absolute;
            left: auto;
            right: -100px;
        }
        .social-buttons {
            position: absolute;
            left: auto;
            right: -108px;
        }
    }
}